#mb-hero-switch {
  display: none;
}

.hero__wrapper {
  min-height: 80vh;
  max-width: 1000px;
  margin: 0px auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  animation: fadeIn 3s;
}

#dark .hero__wrapper {
  transition: color 0.5s ease;
  color: white;
}


.hi {
  font-size: 1.2rem;
  margin: 0;
  margin-bottom: 1rem;
  color: $tertiaryDark;
}

.name {
  font-size: 4rem;
  margin: 0;
  margin-bottom: 1rem;
  font-family: $font-sans;
}

.job-title {
  font-size: 4rem;
  margin: 0;
  color: $secondaryDark;
  font-family: $font-sans;
}

.intro {
  font-size: 1.2rem;
  max-width: 50%;
  margin: 0;
  margin-top: 1.5rem;
  color: $secondaryDark;
}

.hero-btn {
  margin-top: 3.5rem;
}

@media (max-width: 900px) {

  #mb-hero-switch {
    position: absolute;
    display: block;
    top: 5vw;
    left: 5vw;
  }

  .hi {
    font-size: 1rem;
  }

  .name {
    font-size: 2.5rem;
  }

  .job-title {
    font-size: 2.5rem;
  }

  .intro {
    max-width: 100%;
    font-size: 1rem;
  }
}

@media (max-width: 770px) {
  .hero__wrapper {
    min-height: 100vh;
  }
}